.frame {
    background: -webkit-linear-gradient(65deg, #489936, #7F49AD);
    background: -o-linear-gradient(65deg, #489936, #7F49AD);
    background: linear-gradient(25deg, #489936, #7F49AD);
    padding: 1rem;
}

.frame__container {
    margin: 0 auto;

}

.frame__master {
    margin: 0 auto;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    width: 100%;
}

.frame__picture {
    border: 1rem solid snow;
    border-radius: 50%;
    height: 15rem;
    width: 15rem;
    -webkit-box-shadow: 1px 1px 12px black;
            box-shadow: 1px 1px 12px black;
}

.frame__status {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -ms-flex-align: center;
        align-items: center;
    background-color: mintcream;
    padding: 1rem;
    margin: 0 auto;
    width: 6rem;
    height: 1.1rem;
    font-size: 1rem;
    border-radius: 8px;
}

.online {
    height: 1rem;
    width: 1rem;
    background-color: lime;
    -webkit-box-shadow: 1px 1px 2px darkgreen;
            box-shadow: 1px 1px 2px darkgreen;
    border-radius: 50%;
    color: #303030;
}



